<template>
  <transition enter-active-class="slideInRight" leave-active-class="slideOutRight">
    <div id="address">
      <van-address-edit
        show-set-default="true"
        :area-list="areaList"
        show-postal
        show-delete
        show-search-result
        :search-result="searchResult"
        @save="btnSaveHandle"
        @delete="onDelete"
        @change-detail="onChangeDetail"
      />
    </div>
  </transition>
</template>

<script>
console.log("");
import { AddressEdit } from "vant";

export default {
  components: {
    [AddressEdit.name]: AddressEdit
  },
  data() {
    return {
      areaList: {
        province_list: {
          110000: "",
          120000: ""
        },
        city_list: {
          110100: "",
          110200: "",
          120100: "",
          120200: ""
        },
        county_list: {
          110101: "",
          110102: "",
          110105: ""
        }
      },
      searchResult: []
    };
  },

  methods: {
    btnSaveHandle({ name, tel, addressDetail }) {
      // Toast("save");
      this.$router.push({
        path: "/cart/order",
        query: { name, tel, addressDetail }
      });
    },
    onDelete() {
      Toast("delete");
    },
    onChangeDetail(val) {
      if (val) {
        this.searchResult = [
          {
            name: "",
            address: ""
          }
        ];
      } else {
        this.searchResult = [];
      }
    }
  }
};
</script>

<style scoped>
#address {
  width: 100%;
  height: 667px;
  z-index: 1001;
  background: white;
  position: relative;
}
</style>
